<style>

</style>
<template>
  <!--维保工作->维保计划-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">维保工作-维保计划</span>
      </iCol>
    </Row>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Row style="margin-top: 10px;">
        <!--<span>改正期限：</span>-->
        <Select class="grey" v-model="selectedBuildingId" clearable placeholder="所有建筑" @on-change="searchDataByParam"
                style="width: 180px;">
          <Option :value="building.buildingId" :key="building.buildingId" v-for="building in buildingList">
            {{ building.buildingName }}
          </Option>
        </Select>
        <Select class="grey" v-model="taskType" clearable placeholder="计划类型" @on-change="searchDataByParam"
                style="width: 120px; margin-left: 5px;">
          <Option value="PATROL">巡检</Option>
          <Option value="UPKEEP">保养</Option>
          <Option value="DETECT">检测</Option>
        </Select>
        <Select class="grey" v-model="periodType" clearable placeholder="周期类型" @on-change="searchDataByParam"
                style="width: 120px; margin-left: 5px;">
          <Option value="D">每日</Option>
          <Option value="W">每周</Option>
          <Option value="H">每半月</Option>
          <Option value="M">每月</Option>
          <Option value="T">每两月</Option>
          <Option value="S">每季</Option>
          <Option value="B">每半年</Option>
          <Option value="Y">每年</Option>
        </Select>
        <Select class="grey" v-model="isOverTime" clearable placeholder="计划状态" @on-change="searchDataByParam"
                style="width: 120px; margin-left: 5px;">
          <Option value="false">执行中</Option>
          <Option value="true">已完成</Option>
        </Select>
        <Button @click="searchDataByParam" class="grey" style="margin-left:5px;width:80px;">搜索</Button>
        <Button @click="showPlanDetail(undefined)" class="grey" style="margin-left:5px;width:80px; float: right;">添加</Button>
      </Row>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="taskList" class="auto-height-table grey" style="width:100%">
      </Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchData" :total="pageTotal" :pageSize="pageSize" class="default" :current="1"></Page>
        </div>
      </div>
    </div>

    <!--三选确认框弹出层-->
    <Modal v-model="confirmDelete" class="default" title="维保计划删除确认" width="500">
      <Row>
        <iCol span="4" style="text-align: center;">
          <Icon type="help-circled" style="font-size: 35px; color: #4d5f7d;"></Icon>
        </iCol>
        <iCol span="20" style="display: table;">
          <div style="display: table-cell; vertical-align: middle;">
            <Row>
              <span>当前周期内存在已经生成的未完成任务。</span>
            </Row>
            <Row>
              <span>是否同时删除这些任务和维保计划？</span>
            </Row>
          </div>
        </iCol>
      </Row>
      <div slot="footer">
        <Button class="grey" @click="deleteTaskPlanAfterDeleteTasks">同时删除</Button>
        <Button class="grey" style="margin-left: 10px;" @click="deleteTaskPlanWithoutDeleteTasks">仅删除计划</Button>
        <Button class="default"  style="margin-left: 10px; width: 80px;" @click="confirmUpdate = false">取消</Button>
      </div>
    </Modal>
    <!--END三选确认框弹出层-->
  </div>
</template>
<script>
  import niceScroll from 'jquery.nicescroll';
  import server from "../../../config/api";
  import common from "../../../libs/common";

  export default {
    data(){
      return {
        pageTotal: undefined,
        pageSize: 20,
        common: common,
        columns: [
          {
            title: '任务名称',
            align: 'center',
            ellipsis: true,
            key: 'title'
          },
          {
            title: '建筑物',
            align: 'center',
            ellipsis: true,
            key: 'targetBuildingName'
          },
          {
            title: '计划类型',
            width: '60',
            align: 'center',
            render: (h, params) => {
              let result = '未知';
              if(params.row.taskType === 'PATROL') {
                result = '巡检';
              } else if(params.row.taskType === 'UPKEEP') {
                result = '保养';
              } else if(params.row.taskType === 'DETECT') {
                result = '检测';
              }
              return h('span', result);
            }
          },
          {
            title: '周期类型',
            width: '60',
            align: 'center',
            render: (h, params) => {
              let result = '未知';
              if(params.row.periodType === 'D') {
                result = '每日';
              } else if(params.row.periodType === 'W') {
                result = '每周';
              } else if(params.row.periodType === 'H') {
                result = '每半月';
              } else if(params.row.periodType === 'M') {
                result = '每月';
              } else if(params.row.periodType === 'T') {
                result = '每两月';
              } else if(params.row.periodType === 'S') {
                result = '每季度';
              } else if(params.row.periodType === 'B') {
                result = '每半年';
              } else if(params.row.periodType === 'Y') {
                result = '每年';
              }
              return h('span', result);
            }
          },
          {
            title: '计划开始时间',
            width: '100',
            align: 'center',
            render: (h, params) => {
              return h('span', this.common.revertYDateTime(params.row.planStartDate));
            }
          },
          {
            title: '计划截至时间',
            width: '100',
            align: 'center',
            render: (h, params) => {
              if(params.row.planEndDate === null) {
                return h('span', '永久有效');
              }
              return h('span', this.common.revertYDateTime(params.row.planEndDate));
            }
          },
          {
            title: '创建人',
            ellipsis: true,
            width: '60',
            align: 'center',
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.creatorName
                }
              }, params.row.creatorName);
            }
          },
          {
            title: '计划状态',
            align: 'center',
            width: '60',
            render:(h, params) => {
              let now = new Date();
              if(params.row.planEndDate === null || params.row.planEndDate > now.getTime()) {
                return h('span', '执行中');
              } else {
                return h('span', '已完成');
              }
            }
          },
          {
            title: '负责人',
            ellipsis: true,
            width: '60',
            align: 'center',
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.primaryExecutorName
                }
              }, params.row.primaryExecutorName);
            }
          },
          {
            title: '操作',
            align: 'center',
            width: '100',
            render: (h, params) => {
              return h('div', [
                h('i-button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#a9b2fd'
                  },
                  on: {
                    click: () => {
                      this.showPlanDetail(params.row.planId);
                    }
                  }
                }, '编辑'),
                h('i-button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#f9caca'
                  },
                  on: {
                    click: () => {
                      this.deleteTaskPlanConfirm(params.row);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        taskList: [],
        periodType: undefined,
        isOverTime: undefined,
        taskType: undefined,
        selectedBuildingId: undefined,
        currentOrgId: localStorage.current_org_id,
        buildingList: [],
        confirmDelete: false,
        unCompletedTaskIdList: [],
        selectedTaskPlanId: undefined,
      };
    },
    methods: {
      initData: function() {
        // 获取建筑物下拉列表
        this.searchBuildingList();
        this.searchData(1);
      },
      fetchData: function() {
        this.$nextTick(() => {
          $(".auto-height-table .ivu-table-body").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      searchDataByParam: function() {
        this.searchData(1);
      },
      searchBuildingList: function() {
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', this.currentOrgId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("获取建筑物数据失败！");
            return;
          }
          this.buildingList = response.body.data;
        }, (error) => {
          this.$Message.error("获取建筑物数据失败！");
        });
      },
      searchData: function(pageNum) {
        let url = server.api.taskPlan.getPlanDatas + '?orgId=' + this.currentOrgId + '&pageNum=' + pageNum + '&pageSize=' + this.pageSize;
        if(this.selectedBuildingId !== undefined) {
          url += '&buildingId=' + this.selectedBuildingId;
        }
        if(this.taskType !== undefined) {
          url += '&taskType=' + this.taskType;
        }
        if(this.periodType !== undefined) {
          url += '&periodType=' + this.periodType;
        }
        if(this.isOverTime !== undefined) {
          url += '&isOverTime=' + this.isOverTime;
        }
        this.$http.get(url).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("获取计划列表失败！");
            return;
          }
          this.taskList = response.body.data.rows;
          this.pageTotal = response.body.data.pageTotal;
        }, (error) => {
          this.$Message.error("获取计划列表失败！");
        });
      },
      showPlanDetail: function(planId) {
        this.clearYunSession('route_list');
        let page = {};
        page.router = {
          path: '/routine/plan',
        };
        this.pushYunSession('route_list', page);
        // 记录将跳转的页面的数据信息
        page = {
          router: {
            path: '/routine/plan_detail',
          },
          planId: planId
        };
        this.pushYunSession('route_list', page);
        this.$router.push(page.router);
      },
      deleteTaskPlanConfirm: function(taskPlan) {
        this.$Modal.confirm({
          title: '删除确认',
          content: '即将删除维保任务，是否确定删除？',
          okText: '删除',
          onOk: () => {
            this.tryDeleteTaskPlan(taskPlan);
          }
        });
      },
      tryDeleteTaskPlan: function(taskPlan) {
        this.selectedTaskPlanId = taskPlan.planId;
        if(this.isAfterNowPeriod(taskPlan.periodType, taskPlan.planStartDate)) {
          // 计划开始时间在当前周期结束之后
          this.deleteTaskPlan('计划开始时间在当前周期结束之后，直接删除维保计划成功');
          return;
        }
        // 查询是否有任务生成
        this.$http.get(server.api.taskPlan.getNowTasks.replace('{planId}', taskPlan.planId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("获取任务数据失败！");
            return;
          }
          let taskList = response.body.data;
          if(taskList === null || taskList === undefined || taskList.length === 0) {
            // 没有任务生成
            this.deleteTaskPlan('计划开始时间在当前周期结束之前，并且没有生成任务，直接删除维保计划成功。');
            return;
          } else {
            this.unCompletedTaskIdList = [];
            for(let index in taskList) {
              // 从任务列表中获取未完成任务
              if(taskList[index].status !== 'DONE') {
                this.unCompletedTaskIdList.push(taskList[index].taskId);
              }
            }
            if(this.unCompletedTaskIdList.length === 0) {
              // 存在任务，都已经完成
              this.deleteTaskPlan('计划开始时间在当前周期结束之前，并且存在任务，且都已经完成，直接删除维保计划成功。');
              return;
            } else {
              // 存在未完成任务
              this.confirmDelete = true;
            }
          }
        }, (error) => {
          this.$Message.error("获取任务数据失败！");
        });
      },
      deleteTaskPlanWithoutDeleteTasks: function() {
        this.deleteTaskPlan('计划开始时间在当前周期结束之前，并且存在未完成任务，不删除这些任务删除维保计划成功。');
        this.confirmDelete = false;
      },
      deleteTaskPlanAfterDeleteTasks: function() {
        let idList = '';
        for(let index in this.unCompletedTaskIdList) {
          let id = JSON.stringify(this.unCompletedTaskIdList[index]);
          id = id.substr(1, id.length-2);
          idList += id;
          if(parseInt(index) !== this.unCompletedTaskIdList.length -1) {
            idList += ',';
          }
        }
        this.$http.delete(server.api.taskPlan.deleteTask.replace('{taskId}', idList)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error(response.body.meta.message);
            return;
          }
          this.deleteTaskPlan('计划开始时间在当前周期结束之前，并且存在未完成任务，删除这些任务并删除维保计划成功。');
        }, (error) => {
          this.$Message.error("删除任务失败！");
        });
        this.confirmDelete = false;
      },
      deleteTaskPlan: function(successMessage) {
        this.$http.delete(server.api.taskPlan.deleteTaskPlan.replace('{planId}', this.selectedTaskPlanId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error(response.body.meta.message);
            return;
          }
          this.$Message.success(successMessage);
          this.searchData(1);
        },(error) => {
          this.$Message.error('删除维保计划失败！');
        });
      },
      isAfterNowPeriod: function(periodType, planStartDate) {
        // 定义时间为当天的23:59:59
        let today = new Date();
        // let date = new Date(2018,2,8,23,59,59);  2018-03-08 23:59:59 星期四
        let date = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 23, 59, 59);
        switch(periodType) {
          case 'D':
            // 当天23点59分59秒
            break;
          case 'W':
            // 本周六
            date.setDate(date.getDate() + (6 - date.getDay()));
            break;
          case 'H':
            // 判断15号
            if(date.getDate() > 15) {
              // 当月最后一天
              date.setDate(1);
              date.setMonth(date.getMonth() +1);
              date.setDate(date.getDate() - 1);
              break;
            } else {
              // 当月15号
              date.setDate(15);
              break;
            }
          case 'M':
            // 当月最后一天
            date.setDate(1);
            date.setMonth(date.getMonth() +1);
            date.setDate(date.getDate() - 1);
            break;
          case 'T':
            // 第二月最后一天
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 2 ? date.getMonth() + 2 : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'S':
            // 第三个月最后一天
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 3 ? (date.getMonth() + (4 - (date.getMonth() +1) % 3)) : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'B':
            // 第六个月的最后一天
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 6 ? (date.getMonth() + (7 - (date.getMonth() +1) % 6)) : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'Y':
            // 12月最后一天
            date.setDate(1);
            date.setMonth(12);
            date.setDate(date.getDate() - 1);
            break;
        }
        return new Date(planStartDate).getTime() > date.valueOf();
      },
    },
    mounted(){
      this.initData();
      this.fetchData();
    }

  }
</script>



